<template>
  <div id="container" style="background-color: black;">
    <div id="div-topo" style="display: flex; flex-direction: row; align-items: center;justify-content: center;"> 
      <h1 id="titulo">
        <span id="hated">Hated </span>
        <span id="in">in </span>
        <span id="the">the </span>
        <span id="nation">Nation </span>
      </h1>
      <div id="first-text">
        <div ref="threeContainer" class="three-container"></div>
      </div>
    </div>
    <div style="visibility: hidden;" >
      <p style="color: white;">Hated in the Nation " é o sexto e último episódio da terceira temporada da série antológica britânica de ficção científica Black Mirror . Escrito pelo criador e showrunner da série Charlie Brooker e dirigido por James Hawes , estreou na Netflix em 21 de outubro de 2016, junto com o restante da terceira série. É o episódio mais longo de Black Mirror , com 89 minutos.Um episódio de inspiração nórdica noir , "Hated in the Nation" segue os detetives Karin Parke ( Kelly Macdonald ) e Blue Coulson ( Faye Marsay ) enquanto eles investigam uma série de mortes visando os sujeitos do ódio nas redes sociais , nas mãos de Insetos Drones Autônomos. (ADIs) que foram implantadas para combater catástrofes ambientais à medida que as abelhas estão próximas da extinção. Foi filmado principalmente em Londres.Baseado na experiência de Brooker ao receber cartas de ódio depois de escrever uma coluna no Guardian em 2004 que menosprezava George W. Bush , o episódio foi comparado com Arquivo X e explorou temas como vigilância governamental e ambientalismo. Com críticas em sua maioria positivas, os críticos elogiaram a atuação, mas fizeram comentários mistos sobre a história e a duração do episódio. Recebeu classificações medianas nas parcelas do Black Mirror por qualidade.</p>
    </div>
  </div>
</template>
<script>
import * as THREE from 'three';
import ScrollReveal from 'scrollreveal';

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { PointLight } from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

export default {
  name: 'ThreeComponent',
  mounted() {
    const sr = ScrollReveal();

    const container = this.$refs.threeContainer;
    if (!container) {
      console.error('Element with ref "threeContainer" not found');
      return;
    }
    const renderer = new THREE.WebGLRenderer({ alpha: true });

    renderer.setSize(200, 200);
    container.appendChild(renderer.domElement);

    const scene = new THREE.Scene();
    scene.background = null;

    const camera = new THREE.PerspectiveCamera(45, 1, 1, 2000);
    camera.rotation.y = (45 / 100) * Math.PI;
    camera.position.x = 800;
    camera.position.y = 100;
    camera.position.z = 1000;

    const controls = new OrbitControls(camera, renderer.domElement);
    controls.update();

    const light1 = new PointLight(0xc4c4c4, 500000);
    light1.position.set(0, 300, 500);
    scene.add(light1);

    const light2 = new PointLight(0xc4c4c4, 500000);
    light2.position.set(500, 100, 0);
    scene.add(light2);

    const light3 = new PointLight(0xc4c4c4, 500000);
    light3.position.set(0, 100, -500);
    scene.add(light3);

    const light4 = new PointLight(0xc4c4c4, 50000);
    light4.position.set(-500, 300, 500);
    scene.add(light4);

    const gltfLoader = new GLTFLoader();
    gltfLoader.load('/assets/bee.glb', (gltf) => {
      const model = gltf.scene;
      model.scale.set(600, 600, 600);
      model.position.y = -50;
      scene.add(model);

      if (gltf.animations && gltf.animations.length > 0) {
        const mixer = new THREE.AnimationMixer(model);
        gltf.animations.forEach((clip) => {
          mixer.clipAction(clip).play();
        });

        const animate = () => {
          requestAnimationFrame(animate);
          controls.update();
          mixer.update(0.01);
          renderer.render(scene, camera);
        };

        animate();
      } else {
        console.log('Nenhuma animação encontrada no modelo.');
      }
    });

    window.addEventListener('resize', onWindowResize, false);
    function onWindowResize() {
      camera.aspect = 1;
      camera.updateProjectionMatrix();
      renderer.setSize(500, 500);
    }

    const animateSpan = (id) => {

      const span = document.querySelector("#"+id)
      //texto original sem modificações 
      const textSpan = span.textContent;
      if(id == 'hated'){
        const typesSpan = ['Hated','H ted','Ha ed','Hat d']
        let indiceSorteado = Math.floor(Math.random() * typesSpan.length);
        let elementoSorteado = typesSpan[indiceSorteado];

        span.textContent = elementoSorteado+' '
        span.style.setProperty('text-shadow', 'rgb(255, 255, 53) 0px 0px 8px', 'important');

      }else if(id == 'the'){
        const typesSpan = ['t e']
        let indiceSorteado = Math.floor(Math.random() * typesSpan.length);
        let elementoSorteado = typesSpan[indiceSorteado];

        span.textContent = elementoSorteado+' '
        span.style.setProperty('text-shadow', 'rgb(255, 255, 53) 0px 0px 8px', 'important');

      }else if(id == 'nation'){
        const typesSpan = ['N tion','Na ion','Nat on','Nati n']
        let indiceSorteado = Math.floor(Math.random() * typesSpan.length);
        let elementoSorteado = typesSpan[indiceSorteado];

        span.textContent = elementoSorteado+' '
        span.style.setProperty('text-shadow', 'rgb(255, 255, 53) 0px 0px 8px', 'important');

      }
      //voltar com o texto original
      setTimeout(()=>{
        span.textContent =  textSpan + " ";
        span.style.setProperty('text-shadow', 'rgb(255, 255, 53) 0px 0px 13px', 'important');
      },800)
    }

    const animateNeon = () => {
      
      //array de spans (palavras) que serao animados(as)
      const spans = ['hated','the','nation'];

      setInterval(() => {
          //sortear spans que serao animados
          let indiceSorteado = Math.floor(Math.random() * spans.length);
          let elementoSorteado = spans[indiceSorteado];
          animateSpan(elementoSorteado)
      }, 1000);
    };

    animateNeon()

    sr.reveal('#div-topo',{
        duration: 10000,
        dalay:800,
        origin: 'left',
        distance: '1000px',
        easing: 'ease-out',
        mobile: true,
      });
      sr.reveal('#first-text',{
        duration: 10000,
        dalay:800,
        origin: 'left',
        distance: '1000px',
        easing: 'ease-out',
        mobile: true,
      });


  }
};
</script>
<style scoped>

.three-container {
  width: 200px;
  height: 200px;
}
#container{
  width: 100vw;
  height: 100vh;
}
#div-topo{
  background-color: black;
  visibility: hidden;
  width: 100vw;
  height: 100vh;
}

#titulo {
  font-family: monospace; 
  font-size: 40px;
  color: white;
}

#titulo span {
  text-shadow: 0px 0px 13px rgba(255, 255, 53, 1);
  color: #ffffec;
}
</style>